<template>
    <nacl-page-container no-header>
        <!-- <nacl-header>{{ title }}</nacl-header> -->
        <nacl-page-content
            no-footer
            class="content-wrap"
        >
            <div v-if="orderList.length">
                <!-- <div class="top-background">
                    <basic-title
                        color="light"
                        class="title"
                    >
                        订单详情
                    </basic-title>
                </div> -->
                <div v-for="data in orderList" :key="data.id" class="order-item">
                    <nacl-flexbox
                        justify="between"
                        align="center"
                        class="text"
                    >
                        <span>订单号：{{ data.orderSn }}</span>
                        <span>{{ data.orderStatusText }}</span>
                    </nacl-flexbox>
                    <book-large-list
                        class="list"
                        :can-handle="false"
                        @Remove="showAlter"
                    >
                        <book-large-list-item
                            v-for="item in data.goodsList"
                            :key="item.id"
                            :data="item"
                            :order-id="item.id"
                            class="item"
                            @click.native="handleItemClick(item)"
                        />
                    </book-large-list>
                    <div>
                        <nacl-button
                            v-if="isShowCancelOrder(data.orderstatus)"
                            type="primary"
                            radius
                            class="remove-order"
                            @click="showOrderDelete(data.id)"
                        >
                            取消订单
                        </nacl-button>
                    </div>
                    <div>
                        <nacl-button
                            v-if="isShowRefundOrder(data.orderstatus)"
                            type="primary"
                            radius
                            class="remove-order"
                            @click="showOrderRefund(data.id, data.actualPrice)"
                        >
                            申请退款
                        </nacl-button>
                    </div>
                </div>
            </div>
            <div v-else>
                <img
                    class="cart-no-data"
                    src="../assets/cart-no-data.png"
                    width="100%"
                >
                <!-- <p class="no-data-text-1">喔喔!</p>
                <p class="no-data-text-2">还没有订单哟！</p>
                <div class="no-data-btn-wrap">
                    <basic-button
                        radius
                        @click="$router.history.go(-1)"
                    >
                        返回
                    </basic-button>
                </div> -->
            </div>
        </nacl-page-content>
        <alter
            :title="alter.title"
            :text="alter.text"
            :alter-data="alterData"
            :is-show="isShowAlter"
            @clickButton="alter.methods"
        />
    </nacl-page-container>
</template>

<script>
import BasicTitle from '../components/basic-title'
import BasicButton from '../components/basic-button'
import BookLargeList from '../components/book-large-list/index'
import alter from '../components/alter'
import { NaclButton } from 'nacl-ui'
import { getOrderList, orderDelete, orderRefund } from '../services'

export default {
    name: 'index',
    components: { NaclButton, alter, BasicButton, BasicTitle, BookLargeList, BookLargeListItem: BookLargeList.Item },
    data () {
        return {
            orderList: [],
            isShowAlter: false,
            alterData: null,
            showType: this.$route.query.showType || 0,
            alter: {
                title: '',
                text: '',
                methods: ''
            }
        }
    },
    computed: {
        title () {
            const text = ['全部订单', '待付款', '待发货', '待收货', '已完成']
            return text[this.showType]
        }
    },
    created () {
        this.getOrderList()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        isShowCancelOrder (nub) {
            const nubArray = [101]
            return nubArray.indexOf(nub) !== -1
        },
        isShowRefundOrder (nub) {
            const nubArray = [201, 301]
            return nubArray.indexOf(nub) !== -1
        },
        handleItemClick (item) {
            if (item.status === 0) {
                // this.$router.push(`/orderDetail`)
                this.toGetUser();
                window.webkit.messageHandlers.orderDetail.postMessage('');
            }
        },
        showOrderDelete (orderId) {
            this.isShowAlter = true
            this.alterData = {
                orderId
            }
            this.alter = {
                title: '取消订单',
                text: '是否确认取消订单',
                methods: this.orderRemove
            }
        },
        showOrderRefund (orderId, actualPrice) {
            this.isShowAlter = true
            this.alterData = {
                orderId,
                amount: actualPrice,
                reason: '测试账号退款'
            }
            this.alter = {
                title: '申请退款',
                text: '是否确认申请退款',
                methods: this.orderRefund
            }
        },
        async orderRefund (nub, data) {
            this.isShowAlter = false
            if (nub === 0) {
                await orderRefund(data)
                this.$toast('申请已提交')
                this.getOrderList()
            }
        },
        async orderRemove (nub, data) {
            this.isShowAlter = false
            if (nub === 0) {
                await orderDelete(data.orderId)
                this.$toast('取消订单成功')
                this.getOrderList()
            }
        },
        async getOrderList () {
            let data = await getOrderList(this.showType)
            // getOrder().forEach((i) => {
            //     i.books.forEach((b) => {
            //         data.push({ orderId: i.orderId, status: i.status, book: b })
            //     })
            // })
            // console.log(data)
            this.orderList = data
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
.remove-order {
    position: absolute;
    right: 30px;
    bottom: 7px;
  width: 170px;
  height: 45px;
  line-height: 45px;
  border-radius: 10px;
  background: #fff;
  border: 1.5px solid #999;
  color: #999;
}
.top-background {
  padding: 160px 40px;
  height: 200px * 2;
  background-image: linear-gradient(130deg, #f99f00, #db3069);
  box-sizing: border-box;
  color: #ffffff;
  .title {
  }
  .count {
    font-size: 14px * 2;
  }
}
.list {
  // margin-top: 20px;
}
.no-data-text-1 {
  font-size: 20px * 2;
  color: #302c48;
  text-align: center;
}
.no-data-text-2 {
  opacity: 0.7;
  font-size: 15px * 2;
  color: #302c48;
  text-align: center;
}
.no-data-btn-wrap {
  margin-top: 120px;
  padding: 0 240px;
}
.cart-no-data {
  display: block;
  width: 350px;
  margin: 96px auto;
}
.order-item {
    position: relative;
    margin: 20px 20px 10px;
    background: #fff;
    border-radius: 15px;
    .text {
        padding: 20px 40px 0;
    }
    .item {
        box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.2);
        background: #f1f1f1;
    }
}
</style>
