<template>
    <div
        v-if="isShow"
        class="bg"
        @click="isShow = false"
    >
        <div
            class="box"
            @click.stop
        >
            <p class="title">{{ title }}</p>
            <p class="text">{{ text }}</p>
            <nacl-flexbox
                justify="between"
                align="center"
                class="buttons"
            >
                <div
                    class="buttonLeft"
                    @click="clickButton(0)"
                >
                    确定
                </div>
                <div
                    class="buttonRight"
                    @click="clickButton(1)"
                >
                    取消
                </div>
            </nacl-flexbox>
        </div>
    </div>
</template>

<script>
// import { Object } from 'leancloud-storage'

export default {
    name: 'aletr',
    components: {},
    props: {
        alterData: {
            type: Object,
            default: () => { }
        },
        title: {
            type: String,
            default: '题目'
        },
        text: {
            type: String,
            default: '内容'
        },
        isShow: {
            type: Boolean,
            default: false
        }

    },
    methods: {
        clickButton (nub) {
            this.$emit('clickButton', nub, this.alterData)
        }

    }
}
</script>

<style scoped lang="scss">
.bg {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.box {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 500px;
  height: 300px;
  margin: auto;
  background: #fff;
  border-radius: 10px;
  text-align: center;
  color: #333;
}
.title {
  height: 100px;
  line-height: 100px;
  font-size: 36px;
}
.text {
  margin-top: 10px;
  font-size: 30px;
}
.buttons {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  border-top: 1.5px solid #eee;
  div {
    width: 50%;
    font-size: 32px;
  }
  &::after {
    content: " ";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 1.5px;
    height: 100%;
    background: #eee;
  }
}
</style>
